// =================
.section{
    background:url(../images/bg.jpg) no-repeat center center;
    background-size: 100% 100%;
    display: block;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.jSlots-wrapper {
    overflow: hidden;
    display: block;
}

.bg-item{
    position: absolute;
    z-index: 0;

    &.title{
        background: url(../images/title.png) no-repeat center top;
        background-size: contain;
        top: 100px;
        left: 50%;
        margin-left: -504px;
        width: 1000px;
        height: 260px;
        z-index: 99;
    }

    &.board-single{
        background: url(../images/board_single.png) no-repeat center top;
        background-size: contain;
        width: 847px;
        height: 401px;
        top: $board-pos-top;
        @include center-transform-horizontal;
    }

    &.building{
        left: -1px;
        bottom: -1px;
        width: math.div(682,1920) * 100vw;
        height: auto;
        @include no-select;
    }
}

.slot-arena{
    //position: absolute;
    // width: 100vw;
    // height: 100vh;
    width: 1920px;
    height: 1080px;
    @include center-transform-horizontal;
    //left: 0;
    top: 100%;
    bottom: 0;
    //right: 0;
    z-index: 12;
    &.is-show{
        top: 0;
    }
}

.slot-wrap{
    color: #fff;
    font-family: 'Yeseva One', serif;
    font-weight: 400;
    font-size: 288px;
    line-height: 1;
    letter-spacing: .1em;
    text-indent: .1em;
    text-align: center;
    text-shadow: 0 8px 10px rgba(black,.3);
    width: 100%;
    position: relative;
    .name{
        margin-bottom: .25em;
        text-align: center;
    }
    .dept{
        font-size: .7em;
        text-align: center;
        font-weight: 500;
    }
}

.slot{
    display: block;
    margin: 0;
    padding: 0;
    list-style: none;
    .item{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
    }
}

.slot-result{
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    text-shadow: 0 8px 10px rgba(black,.3);
    
}

.empty{
    // background: url(../images/empty.png) no-repeat center center;
    // background-size: contain;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
}

.btn-go{
    background: url(../images/btn_go.png) no-repeat center center;
    background-size: contain;
    cursor: pointer;
    display: block;
    overflow: hidden;
    outline: none;
    border-width: 0;
    position: absolute;
    left: 50%;
    z-index: 30;
    width: 187px;
    height: 187px;
    margin-left: -93px;
    transition: all .025s ease-in-out;
    span{
        position: absolute;
        top: 100%;
    }

    &:active{
        transform: scale(.9);
    }
}

.endtext{
    background: url(../images/cl.png) no-repeat center top;
    background-size: contain;
    position: absolute;
    width: 964px;
    height: 160px;
    top: 50%;
    left: 50%;
    z-index: 20;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    pointer-events: none;
    &.animate__animated{
        opacity: 1;
        pointer-events: auto;
    }
    // &:not(.animate__animated){
    //     transform: translateY(-1000px);
    // }

}

// ============================
// Single
// ============================
$slot-silgle__hight:298px;
.single{
    width: 100%;
    .slot-wrap{
        width: 770px;
        height: $slot-silgle__hight;
        @include center-transform-horizontal;
        top: $board-pos-top + 32px;
    }
    .jSlots-wrapper{
        height: $slot-silgle__hight;
    }
    .slot-result{
        height: $slot-silgle__hight;
    }
    .slot{
        height: $slot-silgle__hight;
        .item{
            height: $slot-silgle__hight;
        }
    }
    // .empty{
    //     transform: translateY(12px);
    // }
}
.arena-single{
    .btn-go{
        top: $board-pos-top + 300px;
    }
    .endtext{
        top: $board-pos-top + 360px;
        width: 1140px;
        height: 208px;
        margin-left: -570px;
    }
}


// control buttons
// ======================
.btn-go.disabled{
    pointer-events: none !important;
}
.ctrls{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 100;
    .col{
        position: absolute;
        bottom: 20px;
    }
    .col-left{
        left: 20px;
    }
    .col-right{
        right: 20px;
    }

    .btn {
        background-color: transparent;
        background: #482307;
        //border: 0 solid #e5e7eb;
        border-radius: .5rem;
        box-sizing: border-box;
        color: #fff;
        column-gap: 1rem;
        display: block;
        font-size: 13px;
        font-weight: 400;
        line-height: 1;
        margin: 6px 0 0 0;
        outline: 2px solid transparent;
        padding: 10px 12px;
        text-align: center;
        text-decoration: none;
        text-transform: none;
        transition: all .1s cubic-bezier(.4, 0, .2, 1);
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        box-shadow: -6px 8px 10px rgba(81,41,10,0.1),0px 2px 2px rgba(81,41,10,0.2);
        &:active {
            background-color: #f3f4f6;
            box-shadow: -1px 2px 5px rgba(81,41,10,0.15),0px 1px 1px rgba(81,41,10,0.15);
            color: #482307;
            transform: translateY(0.125rem);
        }
        &:focus {
            box-shadow: rgba(72, 35, 7, .46) 0 0 0 4px, -6px 8px 10px rgba(81,41,10,0.1), 0px 2px 2px rgba(81,41,10,0.2);
        }
    }
}

#replay{
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    &.is-show{
        display: block;
        width: 100vw;
        height: 100vw;
        z-index: 1000;
    }
}
// waves
// ======================
.waves-wrapper{
    position: absolute;
    bottom:0;
    left: 0;
    right: 0;
    z-index: 0;
    overflow: hidden;

    .waves {
        height: 240px;
        min-height: 240px;
        width: 100%;
    }

    .parallax>use {
        animation: move-forever 50s cubic-bezier(.55, .5, .45, .5) infinite;
    }
    
    .parallax>use:nth-child(1) {
        animation-delay: -2s;
        animation-duration: 14s;
    }
    
    .parallax>use:nth-child(2) {
        animation-delay: -3s;
        animation-duration: 20s;
    }
    
    .parallax>use:nth-child(3) {
        animation-delay: -4s;
        animation-duration: 13s;
    }
    
    .parallax>use:nth-child(4) {
        animation-delay: -5s;
        animation-duration: 20s;
    }
}

@keyframes move-forever {
    0% {
        transform: translate3d(-90px, 0, 0);
    }

    100% {
        transform: translate3d(85px, 0, 0);
    }
}

// fireworks
// ======================
#fireworks{
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 0;
}