* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}

body,
html {
  font-optical-sizing: auto;
  margin: 0;
  padding: 0;
  height: 100%;
}

html {
  font-size: 100%;
}

body {
  font-family: "Noto Sans TC", "PingFang TC", Roboto, "Segoe UI", SegoeUI, Helvetica, Arial;
  background-size: 100% auto;
  overflow: hidden;
  height: 100vw;
}

body {
  background-color: #EA1C23;
}

.loading {
  background-color: #A6121E;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99999;
  /* HTML: <div class="loader"></div> */
}
.loading .loader {
  --w:10ch;
  font-weight: bold;
  font-family: monospace;
  font-size: 30px;
  line-height: 1.2em;
  letter-spacing: var(--w);
  width: var(--w);
  overflow: hidden;
  white-space: nowrap;
  color: #fff;
  animation: l17 2s infinite;
}
.loading .loader:before {
  content: "Loading...";
}
